import React , { Component } from 'react';
import VideoStript from './VideoStript';

class Stript extends Component
{
    state = {
        scrollTop:0
    };
    
   
    componentDidMount(){
        window.addEventListener('scroll', () => this.listenEvent(this.state));  
    };
    

   listenEvent(state) {
    const position = window.pageYOffset;
    this.setState({scrollTop:position});
   };

    
   /*
    scrollPosition(){
        return this.state.scrollTop <= 325 ? "stript2" : "stript1";
    };
  */
    scrollPosition(){
        
        return this.state.scrollTop <= 200 ? "stript2" : "stript1";
    };

    render(){

        return(
            <div className={this.scrollPosition()}>
                <VideoStript height={this.state.scrollTop} />
                    <h1 className="imgTitle" style={{
                        marginLeft:200,
                        position:"absolute",
                        fontSize:150,color:"white",
                        opacity: 0.8
                }}>Goverment &amp; Defence</h1>
            </div>
        );
    };
}


export default Stript;
